<template>
  <div class="container">
    <div class="content">
      <div class="content_title">观看视频时长
        <div class="time_yi">总计时长：{{ allTime }}分钟</div>
        <div class="time_yi">可兑换时长：{{ yeffectTime }}分钟/10分钟</div>
      </div>

      <div class="content_bar" ref="content_bar">
        <div class="bar_me" ref="bar_me"></div>
        <div class="bar_friend" ref="bar_friend"></div>
      </div>
      <div class="content_time">
        <div class="time_me">
          <i></i><span>本人观看时长：{{ userWatchTime }}分钟</span>
        </div>
        <div class="time_friend">
          <i></i><span>好友贡献时长：{{ relateWatchTime }}分钟</span>
        </div>
        <!-- <div class="time_ke">可兑换时长：{{ effectTime }}分钟</div> -->
      </div>

      <div class="free_coin">
        <span></span>
      </div>

      <ul class="content_box">
        <li :class="{box_lq: effectTime >= 1 && exchangeNum < 10, box_ylq: effectTime < 1 || exchangeNum >= 10}" @click="receive(sku1)">
          <div class="flow">
            <div class="flow_up">1分钟</div>
            <div class="flow_down"><img src="../../../assets/images/bi.png" alt=""> X 1</div>
          </div>
          <span>领取</span>
        </li>
        <li :class="{box_lq: effectTime >= 2 && exchangeNum < 9, box_ylq: effectTime < 2 || exchangeNum >= 9}" @click="receive(sku2)">
          <div class="flow">
            <div class="flow_up">2分钟</div>
            <div class="flow_down"><img src="../../../assets/images/bi.png" alt=""> X 2</div>
          </div>
          <span>领取</span>
        </li>
        <li :class="{box_lq: effectTime >= 5 && exchangeNum < 6, box_ylq:  effectTime < 5 || exchangeNum >= 6}" @click="receive(sku3)">
          <div class="flow">
            <div class="flow_up">5分钟</div>
            <div class="flow_down"><img src="../../../assets/images/bi.png" alt=""> X 5</div>
          </div>
          <span>领取</span>
        </li>
        <li :class="{box_lq: effectTime >= 10 && exchangeNum < 1, box_ylq: effectTime < 10 || exchangeNum >= 1}" @click="receive(sku4)">
          <div class="flow">
            <div class="flow_up">10分钟</div>
            <div class="flow_down"><img src="../../../assets/images/bi.png" alt=""> X 10</div>
          </div>
          <span>领取</span>
        </li>
      </ul>
    </div>

    <div class="background" :class='{hide: hide}'></div>
    <div class="notice" :class='{hide: hide}'>
      <div class="notice_explain">恭喜您兑换无忧币成功</div>
      <div class="notice_notes">更多无忧币等您领取</div>
    </div>
    <div class="notice_enter" @click="enter" :class='{hide: hide}'></div>
  </div>
</template>
<script>
export default {
  name: 'Reword',
  props: [],
  data() {
    return {
      hide: 1,
      userInfo: '',
      userCode: '',
      msgid1: '30012000', // 业务类型关键字
      msgid2: '30012015', // 业务类型关键字
      partyid: '80000096', // 第三方的帐号
      version: '1.0', // 版本号
      key: '88cce715cceb4623ad643e3a6ec438dc', // 商户密钥Secret Key
      token: '',
      attendid: '', // 奖品序列号
      // attendid: '',
      // sku1: '8de3d3450dfd4999877900872a3e9aa7', // 测试
      sku1: '79f2eeb056334116a546eadeb6ad134f', // 1枚
      sku2: '6ca1880fa0eb4b68b0b2f296f9e0c972', // 2枚
      sku3: 'fa147e3e88964a539a48dd12757a5b2f', // 5枚
      sku4: '33c9a57f935d45b69ca8a2d2071a7416', // 10枚
      yeffectTime: 0, // 已兑换时长
      effectTime: 0, // 可兑换时长
      userWatchTime: 0, // 我的时长
      relateWatchTime: 0, // 好友贡献时长
      allTime: 0, // 我和好友总时长
      exchangeNum: 0, // 兑换个数
      exchangeTime: 0, // 兑换时长
      exchange: 0,
      noClick: false
    }
  },
  created() {
    try {
      var json = eval('(' + Jegotrip.getUserInfo() + ')')
      this.userCode = json.userCode
      this.init()
    } catch (error) {
      // console.log(error)
    }
  },
  mounted() {
    // this.init()
  },
  methods: {
    init() {
      this.userInfo = eval('(' + Jegotrip.getUserInfo() + ')')
      console.log(this.userInfo)
      this.userCode = this.userInfo.userCode
      // this.attendid = this.userInfo.userCode
      var data = {
        userId: this.userCode
      }
      console.log(data)
      this.axios.post('https://uds-i.cmishow.com:1443/uds/cloud/watch/list?version=1', data).then((res) => {
        console.log('时长条显示数据', res)
        this.effectTime = res.data.data.effectTime
        this.userWatchTime = res.data.data.userWatchTime
        this.relateWatchTime = res.data.data.relateWatchTime
        this.allTime = this.userWatchTime + this.relateWatchTime
        this.yeffectTime = (this.allTime - this.effectTime).toFixed()
        this.exchangeNum = res.data.data.exchangeNum

        if (this.effectTime < this.relateWatchTime) {
          this.relateWatchTime = 0
        }
        if (this.yeffectTime <= 0) {
          this.yeffectTime = 0
        }

        this.$refs.bar_me.style.width = Number(((this.userWatchTime * this.effectTime) / (this.allTime * this.allTime))*100).toFixed(1) + '%'
        this.$refs.bar_friend.style.width = Number(((this.userWatchTime * this.relateWatchTime) / (this.allTime * this.allTime))*100).toFixed(1) + '%'
      }).catch((error) => {
        console.log(error)
      })
    },
    receive(sku) {
      if (!this.noClick) {
        console.log('点击领取')
      } else {
        console.log('防止重复提交')
        return
      }
      this.noClick = true
      if (sku == '79f2eeb056334116a546eadeb6ad134f' && this.effectTime >= 1 && this.exchangeNum < 10) {
        this.exchangeTime = 1
        this.exchange = 1
      } else if (sku == '6ca1880fa0eb4b68b0b2f296f9e0c972' && this.effectTime >= 2 && this.exchangeNum < 9) {
        this.exchangeTime = 2
        this.exchange = 2
      } else if (sku == 'fa147e3e88964a539a48dd12757a5b2f' && this.effectTime >= 5 && this.exchangeNum < 6) {
        this.exchangeTime = 5
        this.exchange = 5
      } else if (sku == '33c9a57f935d45b69ca8a2d2071a7416' && this.effectTime >= 10 && this.exchangeNum < 1) {
        this.exchangeTime = 10
        this.exchange = 10
      } else {
        console.log('条件不符')
        console.log(this.noClick)
        return
      }

      var timestamp = (new Date()).getTime()
      this.attendid = parseInt((Math.random() + 1) * Math.pow(10, 12 - 1))

      if (this.exchangeNum < 10) {
        // 认证接口
        var signature0 = this.SHA1('msgid=' + this.msgid1 + '&partyid=' + this.partyid + '&timestamp=' + timestamp + '&version=' + this.version + '&key=' + this.key).toUpperCase()
        var data0 = {
          msgid: this.msgid1,
          partyid: this.partyid,
          timestamp: timestamp,
          version: this.version,
          signature: signature0
        }
        console.log('msgid=' + this.msgid1 + '&partyid=' + this.partyid + '&timestamp=' + timestamp + '&version=' + this.version + '&key=' + this.key)
        var _this = this
        this.axios.post('https://party.jegotrip.com.cn/third/authParty.action', data0).then((res) => {
          console.log('认证成功', res)
          _this.token = res.data.token

          // 领奖接口
          console.log('attendid=' + this.attendid + '&msgid=' + this.msgid2 + '&msisdn=' + this.userInfo.mobile + '&partyid=' + this.partyid  + '&sku=' + sku + '&timestamp=' + timestamp + '&token=' + this.token  + '&key=' + this.key)
          var signature1 = this.SHA1('attendid=' + this.attendid + '&msgid=' + this.msgid2 + '&msisdn=' + this.userInfo.mobile + '&partyid=' + this.partyid  + '&sku=' + sku + '&timestamp=' + timestamp + '&token=' + this.token  + '&key=' + this.key).toUpperCase()
          var data1 = {
            partyid: this.partyid,
            msgid: this.msgid2,
            token: this.token,
            msisdn: this.userInfo.mobile,
            sku: sku,
            attendid: this.attendid,
            timestamp: timestamp,
            signature: signature1
          }
          // 领取成功
          this.axios.post('https://party.jegotrip.com.cn/third/activity/rewards', data1).then((res) => {
            console.log('领取成功', res)
            if (res.data.retcode == 0) {
              this.hide = 0
            }
          }).catch((error) => {
            console.log(error)
          })

        }).catch((error) => {
          console.log(error)
        })
      }
    },
    enter() {
      this.hide = 1
      var data = {
        userId: this.userInfo.userCode,
        exchangeTime: this.exchangeTime,
        exchangeNum: this.exchange
      }
      console.log(this.userInfo.userCode, '用户ID')
      this.axios.post('https://uds-i.cmishow.com:1443/uds/cloud/watch/exchange?version=1', data).then((res) => {
        console.log('兑换奖励成功', res)
        this.noClick = false
        this.init()
      }).catch((error) => {
        console.log(error)
      })
    },
    // SHA1方法加密
    SHA1(value) {
      var hexcase = 0;
      var sha = hex_sha1(value)
      return sha
      function hex_sha1(s) {
        return binb2hex(core_sha1(AlignSHA1(s)));
      }
      function sha1_vm_test() {
        return hex_sha1("abc") == "a9993e364706816aba3e25717850c26c9cd0d89d";
      }
      function core_sha1(blockArray) {
        var x = blockArray; // append padding
        var w = Array(80);
        var a = 1732584193;
        var b = -271733879;
        var c = -1732584194;
        var d = 271733878;
        var e = -1009589776;
        for (var i = 0; i < x.length; i += 16) // 每次处理512位 16*32
          {
            var olda = a;
            var oldb = b;
            var oldc = c;
            var oldd = d;
            var olde = e;
            for (var j = 0; j < 80; j++) // 对每个512位进行80步操作
            {
              if (j < 16)
                w[j] = x[i + j];
              else
                w[j] = rol(w[j - 3] ^ w[j - 8] ^ w[j - 14] ^ w[j - 16], 1);
              var t = safe_add(safe_add(rol(a, 5), sha1_ft(j, b, c, d)), safe_add(safe_add(e, w[j]), sha1_kt(j)));
              e = d;
              d = c;
              c = rol(b, 30);
              b = a;
              a = t;
            }
            a = safe_add(a, olda);
            b = safe_add(b, oldb);
            c = safe_add(c, oldc);
            d = safe_add(d, oldd);
            e = safe_add(e, olde);
          }
          return new Array(a, b, c, d, e);
      }
      function sha1_ft(t, b, c, d) {
        if (t < 20)
          return (b & c) | ((~b) & d);
        if (t < 40)
          return b ^ c ^ d;
        if (t < 60)
          return (b & c) | (b & d) | (c & d);
        return b ^ c ^ d; // t<80
      }
      function sha1_kt(t) {
        return (t < 20) ? 1518500249 : (t < 40) ? 1859775393 : (t < 60) ? -1894007588 : -899497514;
      }
      function safe_add(x, y) {
        var lsw = (x & 0xFFFF) + (y & 0xFFFF);
        var msw = (x >> 16) + (y >> 16) + (lsw >> 16);
        return (msw << 16) | (lsw & 0xFFFF);
      }
      function rol(num, cnt) {
        return (num << cnt) | (num >>> (32 - cnt));
      }
      function AlignSHA1(str) {
        var nblk = ((str.length + 8) >> 6) + 1,
          blks = new Array(nblk * 16);
        for (var i = 0; i < nblk * 16; i++)
          blks[i] = 0;
        for (i = 0; i < str.length; i++)
          blks[i >> 2] |= str.charCodeAt(i) << (24 - (i & 3) * 8);
          blks[i >> 2] |= 0x80 << (24 - (i & 3) * 8);
          blks[nblk * 16 - 1] = str.length * 8;
          return blks;
      }
      function binb2hex(binarray) {
        var hex_tab = hexcase ? "0123456789ABCDEF" : "0123456789abcdef";
        var str = "";
        for (var i = 0; i < binarray.length * 4; i++) {
          str += hex_tab.charAt((binarray[i >> 2] >> ((3 - i % 4) * 8 + 4)) & 0xF) +
          hex_tab.charAt((binarray[i >> 2] >> ((3 - i % 4) * 8)) & 0xF);
        }
        return str;
      }
    }
  }
}
</script>

