<template>
  <div class="container">
    <div class="activity_header">
      <!-- <img @click="back" src="../../assets/images/back.png" alt="">
      <span>活动中心</span> -->
    </div>
    <div class="activity_container">
      <div class="container_top">
        <div class="container_top_title">看精彩视频，领无忧好礼</div>
        <div class="container_top_content">观看视频累积时长达活动指定条件，即可领取对应的无忧币换领无忧好礼啦！详情见活动规则。</div>
      </div>
      <div class="container_middle">
        <!-- <div class="container_middle_title">查看活动规则<img src="../../assets/images/more_img.png" alt=""></div> -->
        <Reward></Reward>
      </div>
      <div class="container_bottom">
        <div class="container_bottom_title">活动规则</div>
        <div class="container_bottom_content">
          1. 此活动仅针对无忧行APP注册用户（以下简称“用户”）；
          <br>
          2. 用户在无忧行看世界页面观看视频达指定时长即可领取无忧币；
          <br>
          3. 每日观看视频累计达1分钟，可领取1个无忧币；每日观看视频累计达2分钟，可领取2个无忧币；每日观看视频累计达5分钟，可领取5个无忧币；每日观看视频累计达10分钟，可领取10个无忧币；
          <br>
          4. 每位用户于活动期间每日领取上限为10个无忧币；
          <br>
          5. 用户分享视频给好友，好友观看时长的10%将同步计入分享者的观看时长；
          <br>
          6. 每日未兑换或超出当日兑换上限的观看时长，将于次日凌晨12时（北京时间）清零；
          <br>
          7. 爱奇艺和芒果TV跳转观看时长不计入；
          <br>
          8. 用户可于“无忧行APP > 我的 > 无忧币”查看及兑换相应礼品，此活动无忧币兑换条件与细则遵循无忧行APP相关规定；
          <br>
          9. 活动结束时间为2020年1月31日，无忧币限量发放，领完即止，恕不另行通知；
          <br>
          10.此活动最终解释权归中国移动国际有限公司所有。
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Reward from './ActivityPageType/Reward.vue'
export default {
  components: {Reward},
  data() {
    return {
      dataUrl: '',
      fromPage: ''
    }
  },
  props: ['jsonUrl'],
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.from(from.path)
    })
  },
  beforeRouteLeave (to, from, next) {
    next()
  },
  created() {
    var _hmt = _hmt || [];
    (function() {
      var hm = document.createElement("script");
      hm.src = "https://hm.baidu.com/hm.js?4926cf3d2b853ff341b5b083520ef6a1";
      var s = document.getElementsByTagName("script")[0];
      s.parentNode.insertBefore(hm, s);
    })();
    window.scrollTo(0, 0)
  },
  mounted() {
    this.dataUrl = this.$route.params.jsonUrl
  },
  methods: {
    back() {
      if (this.fromPage == '/index/recommend') {
        this.$router.push({
          path: this.fromPage
        })
      } else {
        let detailUrl = this.$route.params.jsonUrl
        this.$router.push({
          name: 'detail',
          params: {
            jsonUrl: detailUrl
          }
        })
      }
    },
    from(fromPage) {
      this.fromPage = fromPage
    }
  }
}
</script>

<style scoped>
.container {
  margin: 0px;
  padding: 0px;
}
.activity_header {
  margin: .34rem .3rem;
}
.activity_header>img {
  width: .48rem;
  height: .48rem;
}
.activity_header>span {
  font-size: .32rem;
  margin-left: .08rem;
  color: #636055;
  font-weight: 600;
  font-style: 'PingFang HK';
}
.activity_container {
  margin: 0;
}
.container_top_title {
  margin-left: .52rem;
  font-size: .32rem;
  color: #636055;
  font-weight: 600;
  font-style: 'PingFang HK';
}
.container_top_content {
  margin-top: .1rem;
  margin-left: .3rem;
  font-size: .26rem;
  color: #636055;
  font-style: 'PingFang HK';
}
.container_middle {
  margin-top: .2rem;
}
.container_middle_title {
  font-size: .24rem;
  color: #B49942;
  margin-left: .3rem;
  line-height: .44rem;
  font-style: 'PingFang HK';
}
.container_middle_title>img {
  width: .48rem;
  height: .48rem;
}
.container_bottom {
  margin-top: .4rem;
}
.container_bottom_title {
  color: #636055;
  font-size: .32rem;
  font-weight: 600;
  margin-left: .52rem;
}
.container_bottom_content {
  width: 6.7rem;
  margin: .2rem .3rem;
  color: #636055;
  font-size: .26rem;
}
</style>



